import Vuex from 'vuex'
import Vue from 'vue';

// import websocketPlugin from '../store/plugins/web-socket.js'
import CommadSet from '../mission/commands.json';
import mission from '../mission/misison.js';

Vue.use(Vuex);

export default new Vuex.Store({
  strict: true,

  state: {

    PollingState: CommadSet.CMD_GET_UNIT_INFO,

    websocketIsConnected: false,
    websocket: null,
    timerPromise: null,

    secretKey: "",

    // IP Information
    ipInfo: {
      IsDhcp: true,
      Address: "192.168.77.77",
      Subnet: "255.255.255.0",
      Gateway: "192.168.77.1",
      MAC: "48:65:EE:48:01:02"
    },

    // Unit Options
    unitOptions: {
      Tx: {
        StepAtten: {
          Min: 0.0,
          Max: 31.75,
          Rez: 0.25
        },

        LoFreq: {
          PresentEnabled: true,
          RangeEnabled: false,
          Presets: [28.05, 29.00]
        },

        Alc: {
          Enabled: false,
        }
      }
    },

    // Unit Status
    unitStatus: {
      TxStepAtten: 2.75,
      TxLoFreq: 28.05,
      TxPower: 31.5,
      TxRefLocked: true,
      SwTxEnabled: true,

      RxStepAtten: 0.00,
      RxLoFreq: 0.0,
      RxPower: 0,
      RxRefLocked: true,
      SwRxEnabled: false,

      FanSpeed: 2130,
      Temperature: 53.2,

      TempCompEnabled: true,

      Faults: []
    },
    savedUnitStatus: {
      TxStepAtten: 0.00,
      TxLoFreq: 28.05,
      TxPower: 31.5,
      TxRefLocked: true,
      SwTxEnabled: true,

      RxStepAtten: 0.00,
      RxLoFreq: 0.0,
      RxPower: 0,
      RxRefLocked: true,
      SwRxEnabled: false,

      FanSpeed: 2130,
      Temperature: 53.2,

      TempCompEnabled: true,
      Faults: []
    },

    // Unit Information
    unitInfo: {
      Model: "KAA-100B-A1-1130110",
      Serial: "SN2310",
      Firmware: "20210102BD04",
      Bootloader: "0.2.2"
    },

    // ALC Settings
    alcSettings: {
      SetPoint: 32.1,
      HighAlarm: 30.0,
      LowAlarm: 20.2,

      HighFault: 40.0,
      LowFault: 40.0,

      IsEnabled: false,
    },

    faultDefinitions: {
      HighRfPower:41.2,
      HighRfOption: 2,

      LowRfPower: 21.5,
      LowRfOption: 2,

      ReflectedPower: 35.6,
      ReflectedOption: 2
    },

    // NTP Options
    clockNtp: {

      PrimaryAddr: "192.168.10.100",
      SecondaryAddr: "192.168.10.100",
      
      IsManual: true,
      ManualTime: "2021-11-10T13:01:15",

      UtcOffsetMinute: 90,

      UpdateDaily: false,
      IsSynced: false
    },

    // Serial Interface - RS485, Baudrate
    serialInterface: {
      IsRs485: true,
      SelectedBaudrate: 115200,
      BaudrateOptions: [115200, 38400, 9600]
    },

    // RF Startup Settings
    PowerOnState: false,
    
    PowerConversion: 0,

    alias: "Mission BUC",
    desiredPower: 0,

  },

  getters: {
    getSecretKey: function(state) {
      return state.secretKey;
    },

    getTimerPromise: function(state) {
      return state.timerPromise;
    },

    getPollingState: function(state) {
      return state.PollingState;
    },

    getUnitInfo: function(state) {
      return state.unitInfo;
    },

    getTxOption: function(state) {
      return state.unitOptions.txOption;
    },

    getUnitStatus: function(state) {
      return state.unitStatus;
    },

    getIpInfo: function(state) {
      console.log(state.ipInfo);
      return state.ipInfo;
    },

    getSerialOptions: function(state) {
      return state.serialInterface;
    },

    getClockNtp: function(state) {
      return state.clockNtp;
    },

    getUnitAlias: function(state) {
      return state.alias;
    },

    getPowerConversion: function(state) {
      return state.PowerConversion;
    },
    
    getPowerOnState: function(state) {
      return state.PowerOnState;
    },

    getAlcStatus: function(state) {
      return state.alcSettings;
    },

    getFaultDefinitions: function(state) {
      return state.faultDefinitions;
    },

    getDesiredPower: function(state) {
      return state.desiredPower;
    }

  },

  mutations: {
    GET_UNIT_INFO(state) {
      if (state.websocketIsConnected) {
        console.log('Get unit info');
        var msg = mission.GetUnitInfoCommand();
        msg.key = state.secretKey;
        state.websocket.send(JSON.stringify(msg));
      }
    },
    GET_IP_INFO(state) {
      if (state.websocketIsConnected) {
        console.log('Get IP info');
        var msg = mission.GetIpInfoCommand();
        msg.key = state.secretKey;
        // state.websocket.send(JSON.stringify(msg));
      }
    },
    GET_NTP_OPTIONS(state) {
      if (state.websocketIsConnected) {
        console.log('Get NTP Options');
        var msg = mission.GetNtpCommand();
        msg.key = state.secretKey;
        // state.websocket.send(JSON.stringify(msg));
      }
    },
    GET_POWER_CONVERSION(state) {
      if (state.websocketIsConnected) {
        console.log('Get Power Conversion');
        var msg = mission.GetPowerConversionCommand();
        msg.key = state.secretKey;
        // state.websocket.send(JSON.stringify(msg));
      }
    },
    GET_UNIT_ALIAS(state) {
      if (state.websocketIsConnected) {
        console.log('Get Unit Alias');
        var msg = mission.GetUnitAliasCommand();
        msg.key = state.secretKey;
        // state.websocket.send(JSON.stringify(msg));
      }
    },
    GET_POWER_ON_STATE(state) {
      if (state.websocketIsConnected) {
        console.log('Get Power On State');
        var msg = mission.GetPowerOnStateCommand();
        msg.key = state.secretKey;
        // state.websocket.send(JSON.stringify(msg));
      }
    },
    GET_BAUDRATE_OPTIONS(state) {
      if (state.websocketIsConnected) {
        console.log('Get baud rate');
        var msg = mission.GetBaudrateOptionsCommand();
        msg.key = state.secretKey;
        // state.websocket.send(JSON.stringify(msg));
      }
    },
    GET_UNIT_OPTIONS(state) {
      if (state.websocketIsConnected) {
        console.log('Get unit options');
        var msg = mission.GetUnitOptionsCommand();
        msg.key = state.secretKey;
        // state.websocket.send(JSON.stringify(msg));
      }
    },
    GET_USER_STATUS(state) {
      if (state.websocketIsConnected) {
        console.log('Get user status');
        var msg = mission.GetUserStatusCommand();
        msg.key = state.secretKey;
        // state.websocket.send(JSON.stringify(msg));
      }
    },

    SEND_MESSAGE_TO_WEBSOCKET(state, msg) {
      if (state.websocketIsConnected) {
        msg.key = state.secretKey;
        try {
          console.log('Sending message', msg);
          state.websocket.send(JSON.stringify(msg));
        } 
        catch (exceptions) {
          console.log(exceptions);
        }
      }
    },

    SET_WEBSOCKET(state, newWebsocket) {
      state.websocket = newWebsocket;
    },
    SET_WEBSOCKET_CONNECTED(state, connected) {
      state.websocketIsConnected = connected;
      if (connected == false && state.websocket != null) {
        state.websocket.close();
      }
    },
    
    SET_SECRET_KEY(state, key) {
      state.secretKey = key;
    },

    SET_POLLING_STATE(state, pollState) {
      state.PollingState = pollState;
    },

    SET_PROMISE(state, promise) {
      state.timerPromise = promise;
    },
    CANCEL_PROMISE(state) {
      if (state.timerPromise != null) {
        // console.log('Canceling timer');
        clearInterval(state.timerPromise);
        console.log('Canceled timer');
      }
    },

    SET_TEST_FAULT(state) {
      state.unitStatus.Faults = ['OverTemp'];
    },
    RESET_TEST_FAULT(state) {
      state.unitStatus.Faults = [];
    },
    
    // Processing Received Data
    PROCESS_RECEIVED_DATA(state, details) {
      switch (details.cmd) {

        case CommadSet.CMD_GET_UNIT_INFO: {
          state.unitInfo = details.UnitInfo;
        }
        break;

        case CommadSet.CMD_GET_IP_INFO: {
          state.ipInfo = details.UnitIp;
        }
        break;

        case CommadSet.CMD_GET_NTP: {
          state.clockNtp = details.ClockNtp;
        }
        break;

        case CommadSet.CMD_GET_POWER_CONVERSION: {
          state.PowerConversion = details.PowerConversion;
        }
        break;

        case CommadSet.CMD_GET_POWER_ON_STATE: {
          state.PowerOnState = details.PowerOnState;
        }
        break;

        case CommadSet.CMD_SET_BAUDRATE: {
          state.serialInterface = details.SerialInterface;
        }
        break;

        case CommadSet.CMD_GET_ALIAS: {
          state.alias = details.Alias;
        }
        break;

        case CommadSet.CMD_GET_USER_STATUS: {
          console.log(details);
        }
        break;

        case CommadSet.CMD_GET_UNIT_OPTIONS: {
          // state.PollingState = CommadSet.CMD_GET_UNIT_OPTIONS;
        }
        break;
      }
    },

    
    // For Demo Purposes
    SET_POWER_UNIT(state, unit) {
      state.PowerConversion = unit;
    },
    SET_SW_TX_ENABLE(state, isEnabled) {
      state.unitStatus.SwTxEnabled = isEnabled;
    },
    SET_TX_ATTENUATOR(state, atten) {
      state.unitStatus.TxStepAtten = atten;
    },
    SET_IP_INFO(state, ipInfo) {
      state.ipInfo = ipInfo;
      console.log(ipInfo);
    },
    SET_BAUDRATE_INFO(state, baudrate) {
      state.serialInterface.SelectedBaudrate = baudrate;
    },
    SET_SERIAL_INTERFACE(state, is485) {
      state.serialInterface.IsRs485 = is485;
    },
    SET_POWER_ON_STATE(state, onState) {
      state.PowerOnState = onState;
    },
    SET_NTP(state, ntp) {
      state.clockNtp = ntp;
    },
    SET_FAULT_DEFINITIONS(state, faults) {
      state.faultDefinitions = faults;
    },
    SET_DESIRED_POWER(state, power) {
      state.desiredPower = power;
    },
    SET_UNIT_ALIAS(state, alias) {
      state.alias = alias;
    },

    // ALC
    SET_ALC_ENABLE(state, enable) {
      state.alcSettings.IsEnabled = enable;
    },
    SET_ALC_OPTIONS(state, options) {
      state.alcSettings.SetPoint = options.SetPoint;

      state.alcSettings.HighAlarm = options.HighAlarm;
      state.alcSettings.LowAlarm = options.LowAlarm;

      state.alcSettings.HighFault = options.HighFault;
      state.alcSettings.LowFault = options.LowFault;
    },

  },

  actions: {

    // Get Unit Info
    PerformGetUnitInfo({commit}) {
      // console.log(commit, mission.GetUnitInfoCommand());
      commit("SEND_MESSAGE_TO_WEBSOCKET", mission.GetUnitInfoCommand());
    },

    PerformSetIpInfo({commit}, newIpInfo) {
      commit("SET_IP_INFO", newIpInfo);
      if (newIpInfo != null && newIpInfo != undefined) {
        commit("SEND_MESSAGE_TO_WEBSOCKET", mission.SetIpInfoCommand(newIpInfo));
      }
    },

    PerformSetStepAtten({commit}, newStepAtten) {
      commit("SET_TX_ATTENUATOR", newStepAtten);
      if (newStepAtten > 0) {
        commit("SEND_MESSAGE_TO_WEBSOCKET", mission.SetAttenuatorCommand(newStepAtten));
      }
    },

    PerformSetSwTxEnabled({commit}, isEnabled) {
      commit("SET_SW_TX_ENABLE", isEnabled);
      if (typeof(isEnabled) == Boolean) {
        commit("SEND_MESSAGE_TO_WEBSOCKET", mission.SetTxEnableCommand(isEnabled));
      }
    },

    PerformSetBaudrate({commit}, baudrate) {
      commit("SET_BAUDRATE_INFO", baudrate);
      if (baudrate != null && baudrate != undefined) {
        commit("SEND_MESSAGE_TO_WEBSOCKET", mission.SetBaudrateOptionsCommand(baudrate));
      }
    },

    PerformSetSerialInterface({commit}, is485) {
      commit("SET_SERIAL_INTERFACE", is485);
      if (typeof(is485) == Boolean) {
        commit("SEND_MESSAGE_TO_WEBSOCKET", mission.SetSerialInterfaceCommand(is485));
      }
    },
    
    PerformSetPowerOnState({commit}, bootup) {
      commit("SET_POWER_ON_STATE", bootup);
      if (typeof(bootup) == Boolean) {
        commit("SEND_MESSAGE_TO_WEBSOCKET", mission.SetPowerOnStateCommand(bootup));
      }
    },

    PerformSetNtp({commit}, ntp) {
      commit("SET_NTP", ntp);
      if (ntp != null && ntp != undefined) {
        commit("SEND_MESSAGE_TO_WEBSOCKET", mission.SetNtpCommand(ntp));
      }
    },

    PerformSetFaultDefinitions({commit}, faults) {
      console.log(faults);
      commit("SET_FAULT_DEFINITIONS", faults);
      if (faults != null && faults != undefined) {
        commit("SEND_MESSAGE_TO_WEBSOCKET", mission.SetFaultDefinitionsCommand(faults));
      }
    },

    PerformSetDesiredPower({commit}, power) {
      commit("SET_DESIRED_POWER", power);
      if (power > 0) {
        commit('SEND_MESSAGE_TO_WEBSOCKET', mission.SetDesiredPowerCommand(power));
      }
    },

    PerformSetPowerConversion({commit}, conversion) {
      commit('SET_POWER_UNIT', conversion);
      if (conversion >= 0 && conversion <= 2) {
        commit('SEND_MESSAGE_TO_WEBSOCKET', mission.GetPowerConversionCommand(conversion));
      }
    },

    // ALC
    PerformSetAlcEnable({commit}, alcEnable) {
      commit("SET_ALC_ENABLE", alcEnable);
    },
    PerformSetAlcOptions({commit}, options) {
      commit("SET_ALC_OPTIONS", options);
    },

    // Test Faults
    PerformTestFaults({commit}) {
      commit("SET_TEST_FAULT");
    },
    PerformResetFaults({commit}) {
      setTimeout(() => {
        commit("RESET_TEST_FAULT");
      }, 5000);
    },

    PerformSetUnitAlias({commit}, newName) {
      commit("SET_UNIT_ALIAS", newName);
      if (newName != null && newName.length > 0) {
        commit('SEND_MESSAGE_TO_WEBSOCKET', mission.SetUnitAliasCommand(newName));
      }
    },

    PerformSetSecretKey({commit}, key) {
      commit("SET_SECRET_KEY", key);
    },

    PerformLogOut({commit}) {
      commit("CANCEL_PROMISE");
      commit('SET_WEBSOCKET_CONNECTED', false);
      commit('SET_POLLING_STATE', '21');
    },

    
  },

  // plugins: [websocketPlugin]

})
