import CommandSet from '../mission/commands.json';

export default {

  CreateCommand(cmd, data) {
    var msg = {
      cmd: cmd
    };

    if (data != null && data != undefined) {
      msg.CmdData = data;
    }

    return msg;
  },

  // Get Commands
  GetUnitInfoCommand() {
    return this.CreateCommand(CommandSet.CMD_GET_UNIT_INFO, null);
  },

  // IP Configurations
  GetIpInfoCommand() {
    return this.CreateCommand(CommandSet.CMD_GET_IP_INFO, null);
  },
  SetIpInfoCommand(newIp) {
    return this.CreateCommand(CommandSet.CMD_SET_IP_INFO, newIp);
  },

  GetUserStatusCommand() {
    return this.CreateCommand(CommandSet.CMD_GET_USER_STATUS, null);
  },

  GetUnitOptionsCommand() {
    return this.CreateCommand(CommandSet.CMD_GET_UNIT_OPTIONS, null);
  },

  // Unit Alias
  GetUnitAliasCommand() {
    return this.CreateCommand(CommandSet.CMD_GET_ALIAS, null);
  },
  SetUnitAliasCommand(alias) {
    return this.CreateCommand(CommandSet.CMD_SET_ALIAS, alias);
  },

  // NTP
  GetNtpCommand() {
    return this.CreateCommand(CommandSet.CMD_GET_NTP, null);
  },
  SetNtpCommand(ntp) {
    return this.CreateCommand(CommandSet.CMD_SET_NTP, ntp);
  },

  // Baudrate
  GetBaudrateOptionsCommand() {
    return this.CreateCommand(CommandSet.CMD_SET_BAUDRATE, null);
  },
  SetBaudrateOptionsCommand(baudrate) {
    return this.CreateCommand(CommandSet.CMD_SET_BAUDRATE, baudrate);
  },

  // Set Serial Interface
  SetSerialInterfaceCommand(is485) {
    return this.CreateCommand(CommandSet.CMD_SET_SERIAL_INTERFACE, is485);
  },

  // Power Conversion
  GetPowerConversionCommand() {
    return this.CreateCommand(CommandSet.CMD_GET_POWER_CONVERSION, null);
  },
  SetPowerConversionCommand(powerUnit) {
    return this.CreateCommand(CommandSet.CMD_SET_POWER_CONVERSION, powerUnit);
  },

  // Power On State
  GetPowerOnStateCommand() {
    return this.CreateCommand(CommandSet.CMD_GET_POWER_ON_STATE, null);
  },
  SetPowerOnStateCommand(powerOn) {
    return this.CreateCommand(CommandSet.CMD_GET_POWER_ON_STATE, powerOn);
  },

  // Set TX Attenuator
  SetAttenuatorCommand(number) {
    return this.CreateCommand(CommandSet.CMD_SET_TX_ATTENUATOR, parseFloat(number).toFixed(2));
  },
  // Set TX Enable
  SetTxEnableCommand(isEnabled) {
    return this.CreateCommand(CommandSet.CMD_SET_TX_ENABLE, isEnabled);
  },

  // Fault Definitions
  GetFaultDefinitionsCommand() {
    return this.CreateCommand(CommandSet.CMD_GET_FAULT_DEFINITIONS, null);
  },
  SetFaultDefinitionsCommand(faultDefinitions) {
    return this.CreateCommand(CommandSet.CMD_GET_FAULT_DEFINITIONS, faultDefinitions);
  },

  // Desired Power
  SetDesiredPowerCommand(power) {
    return this.CreateCommand(CommandSet.CMD_SET_DESIRED_POWER, parseFloat(power).toFixed(2));
  }
}